/* fonts */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Schoolbell&display=swap');

/* desktop column sizes */
:root{
  --col-name: 150px;
  --col-team: 90px;
  --col-pos: 80px;
  --col-conf: 90px;
  --col-ht: 80px;
  --col-age: 70px;
  --col-num: 60px;
  --row-h: 60px;
}

/* base */
body{ margin:0; background:#f5ecdf; color:#000; font-family:'Roboto',sans-serif; }
.mobile-grid-head{ display:none; }

.App{
  width:100%;
  text-align:center;
  padding:20px;
  display:flex; flex-direction:column; align-items:center; justify-content:flex-start;
}

/* === BRAND (Sue head + bird-le) === */
.brand{ display:flex; align-items:center; justify-content:center; gap:10px; margin:6px 0 8px; }
.brand-head{
  width:156px; height:156px; object-fit:cover;
  transform: rotate(-12deg);
  border:none !important;
  border-radius:0 !important;
  background:transparent !important;
  filter: drop-shadow(0 2px 3px rgba(0,0,0,.25));
}
.brand-word{ display:flex; align-items:baseline; }
.brand-text,.brand-dash,.brand-le{
  font-family:'Schoolbell', cursive;
  font-size:48px; line-height:1; letter-spacing:1px;
  color:#000; text-shadow:none;
}
.brand-dash{ margin:0 4px; }
@media (max-width:420px){
  .brand-head{ width:46px; height:46px; }
  .brand-text,.brand-dash,.brand-le{ font-size:40px; }
}

/* form + input */
form{ display:flex; flex-direction:column; align-items:center; position:relative; }
input[type="text"]{
  padding:8px; font-size:1.2em; width:100%; max-width:600px; margin-bottom:20px;
  border:2px solid #000; border-radius:5px; background:#fff; color:#000; text-align:center; box-sizing:border-box;
}
input[type="text"]:focus{ outline:none; border-color:#FF5910; box-shadow:0 0 0 2px #ff591055; transition:border-color .2s, box-shadow .2s; }
input[type="text"].disabled-input{ background:#f2f2f2; border:2px solid #ccc; color:#666; cursor:not-allowed; }

/* buttons */
button{
  background:#FFD700; color:#1A1A1A; padding:10px 20px; font-size:1.2em;
  border:none; border-radius:5px; cursor:pointer; margin-top:10px; font-family:'Chakra Petch',serif;
  box-shadow:0 2px 8px rgba(0,0,0,0.12);
}
button:hover{ background:#FFCC00; }
button:active{ transform:scale(0.96); transition:transform .1s; }
button:disabled{ background:#ccc; color:#666; cursor:not-allowed; }

/* guesses meter – orange pills */
.guesses-meter{
  position:relative; display:flex; gap:6px; align-items:center; justify-content:center;
  margin:6px 0 10px;
}
.gm-seg{
  width:32px; height:10px; border-radius:6px;
  background:#ffe1d1; box-shadow: inset 0 0 0 2px #ff591033;
  transition: transform .15s, background .15s, opacity .15s;
}
.gm-seg.on{ background:#FF5910; box-shadow: inset 0 0 0 2px #c94000; }
.gm-seg.off{ opacity:.35; }
.gm-seg.last{ transform:scale(1.02); }
.guesses-meter.hit .gm-seg.last{ transform:scale(1.12); }
.gm-label{
  position:absolute; top:-20px; left:50%; transform:translateX(-50%);
  font-weight:700; font-family:'Chakra Petch',serif; font-size:14px; color:#333;
}
.guesses-meter.danger .gm-seg.on{ background:#ff8a66; }

/* shot clock – dark pill with red digits */
.shot-clock{
  display:inline-flex; align-items:center; justify-content:center;
  padding:6px 12px; background:#111; border:4px solid #333; border-radius:10px;
  box-shadow: inset 0 0 10px rgba(255,0,0,0.25), 0 2px 8px rgba(0,0,0,0.15);
}
.shot-clock .digits{
  font-family:'Schoolbell', cursive; font-weight:700; font-size:28px; letter-spacing:1px;
  color:#ff3b30; text-shadow:0 0 10px rgba(255,59,48,0.7); line-height:1;
}
.blinking-colon{ animation: shotBlink 1s steps(1,end) infinite; }
@keyframes shotBlink{ 50% { opacity:.15; } }
@media (max-width:420px){ .shot-clock .digits{ font-size:24px; } }

/* help */
.help-icon{
  position:fixed; top:12px; right:18px; z-index:2000;
  width:36px; height:36px; background:#fff; color:#333; border-radius:50%;
  box-shadow:0 2px 8px rgba(0,0,0,0.12);
  display:flex; align-items:center; justify-content:center; font-size:26px; font-weight:bold; cursor:pointer;
}
.help-icon:hover{ background:#f0f0f0; }
.help-modal-overlay{ position:fixed; inset:0; background:rgba(0,0,0,0.35); z-index:2000; display:flex; align-items:center; justify-content:center; }
.help-modal{
  background:#fff; border-radius:10px; padding:28px 24px 20px; max-width:380px; width:90%;
  box-shadow:0 4px 24px rgba(0,0,0,0.18); position:relative; animation: fadeIn .2s;
}
.help-modal h2{ margin-top:0; font-size:1.3em; margin-bottom:12px; }
.help-modal ul{ padding-left:18px; margin-bottom:14px; }
.help-modal p{ font-size:.98em; margin:0; }
.help-modal-close{ position:absolute; top:8px; right:12px; background:none; border:none; font-size:22px; cursor:pointer; color:#888; }
.help-modal-close:hover{ color:#222; }
@keyframes fadeIn{ from{opacity:0;transform:scale(0.97)} to{opacity:1;transform:scale(1)} }

/* table shell (back to light) */
.game-info{ margin-top:10px; z-index:1; }
.game-info h3{
  border-bottom:2px solid #FF5910; display:inline-block; padding-bottom:2px; margin-bottom:12px;
  color:#000; font-size:1.15em;
}
.info-box{
  margin-top:10px; padding:10px; border:2px solid #000; border-radius:10px;
  background:#fff; box-shadow:0 2px 8px rgba(0,0,0,0.04); overflow-x:auto; -webkit-overflow-scrolling:touch; position:relative;
}
.info-box-inner{
  display:flex; flex-direction:column;
  min-width: calc(var(--col-name) + var(--col-team) + var(--col-pos) + var(--col-conf) + var(--col-ht) + var(--col-age) + var(--col-num));
}

/* header row + columns */
.info-item-wrapper{ display:flex; gap:0; margin-bottom:10px; align-items:stretch; height:var(--row-h); min-height:var(--row-h); position:relative; }
.title{ margin-bottom:10px; font-weight:bold; text-align:center; font-size:1em; font-family:'Chakra Petch',serif; color:#000; }
.title .info-label{ text-align:center; color:#000; display:flex; align-items:center; justify-content:center; }

.title .info-label:nth-child(1), .info-item-wrapper .name-cell { flex:0 0 var(--col-name); }
.title .info-label:nth-child(2), .info-item-wrapper .info-item:nth-child(2) { flex:0 0 var(--col-team); }
.title .info-label:nth-child(3), .info-item-wrapper .info-item:nth-child(3) { flex:0 0 var(--col-pos); }
.title .info-label:nth-child(4), .info-item-wrapper .info-item:nth-child(4) { flex:0 0 var(--col-conf); }
.title .info-label:nth-child(5), .info-item-wrapper .info-item:nth-child(5) { flex:0 0 var(--col-ht); }
.title .info-label:nth-child(6), .info-item-wrapper .info-item:nth-child(6) { flex:0 0 var(--col-age); }
.title .info-label:nth-child(7), .info-item-wrapper .info-item:nth-child(7) { flex:0 0 var(--col-num); }

/* rows */
.guess{
  border:2px solid #d1d5db; border-radius:8px; padding-top:0;
  display:flex; justify-content:space-between; gap:0; align-items:stretch;
  height:var(--row-h); min-height:var(--row-h); width:100%;
  background:#fff; overflow:hidden; font-family:'Chakra Petch',serif;
  animation: guessFadeIn .4s; box-shadow:0 4px 16px rgba(34,34,34,0.10);
}
.guess .info-item{
  min-width:0; text-align:center; padding:0; color:#222; background:#fff; border:none;
  font-weight:600; font-size:1.08em; display:flex; align-items:center; justify-content:center; height:100%; width:100%;
}
.guess .info-item:not(:last-child){ border-right:1.5px dotted #b0b3b8; }

.guess .info-item.correct{ background:#4caf50; color:#fff; }
.guess .info-item.close{ background:#ffd600; color:#222; }
.guess .info-item.incorrect{ background:#e0e0e0; color:#222; }

.guess .info-item.name-cell{ background:#fff; color:#222; }

.guess .info-item.ht-cell, .guess .info-item.age-cell, .guess .info-item.num-cell{
  flex-direction:column; align-items:center; justify-content:center;
}
.guess .info-item.ht-cell span, .guess .info-item.age-cell span, .guess .info-item.num-cell span{
  margin-left:0; margin-top:2px; display:block;
}

/* helpers */
.correct{ color:green; } .incorrect{ color:red; } .yellow{ color:orange; }
@keyframes moveUpDown{ 0%{transform:translateY(0)} 50%{transform:translateY(-5px)} 100%{transform:translateY(0)} }
@keyframes moveDownUp{ 0%{transform:translateY(0)} 50%{transform:translateY(5px)} 100%{transform:translateY(0)} }
.arrow-up{ display:inline-block; animation: moveUpDown 2s infinite; margin-left:5px; }
.arrow-down{ display:inline-block; animation: moveDownUp 2s infinite; margin-left:5px; }
/* nudge arrows in age column a touch */
.age-cell .arrow-up, .age-cell .arrow-down { margin-top:2px; }

/* images */
.silhouette{ width:350px; height:254px; display:block; margin-top:10px; border:2px solid #000; background:#ebebeb; }
.silhouette-container{ display:flex; flex-direction:column; align-items:center; position:relative; max-height:700px; z-index:2; }
.silhouette,.actual{ width:310px; height:254px; object-fit:cover; border:2px solid #000; }
.actual{ background:#ebebeb; }

/* typeahead */
.search-container{ width:auto; max-width:600px; position:relative; margin-bottom:20px; display:flex; flex-direction:column; align-items:center; box-sizing:border-box; }
.suggestions{
  position:absolute; top:60px; left:0; right:0; width:100%;
  background:#fff; border:1.5px solid #222; border-radius:10px;
  box-shadow:0 8px 32px rgba(0,0,0,0.12), 0 1.5px 4px rgba(255,89,16,0.08);
  max-height:220px; overflow-y:auto; z-index:1000; opacity:0; transform:translateY(-10px) scale(0.98); pointer-events:none;
  transition: opacity .25s cubic-bezier(0.4,0,0.2,1), transform .25s cubic-bezier(0.4,0,0.2,1);
}
.suggestions.open{ opacity:1; transform:translateY(0) scale(1); pointer-events:auto; }
.suggestions::-webkit-scrollbar{ display:none; }
.suggestion{ padding:12px 18px; cursor:pointer; font-family:'Chakra Petch',serif; font-size:1.08em; color:#222; background:transparent; transition: background .18s, color .18s, transform .15s; }
.suggestion:hover,.suggestion:focus{ background:#ffe5d0; color:#FF5910; transform:scale(1.03); outline:none; }
.suggestion:not(:last-child){ border-bottom:1px solid #f5ecdf; }

/* small fades */
@keyframes fadeInMain{ from{opacity:0; transform:translateY(16px)} to{opacity:1; transform:translateY(0)} }
@keyframes guessFadeIn{ from{opacity:0} to{opacity:1} }
.guess.correct-row{ background:#eaffea !important; border-left:4px solid #4caf50; }
.guess.incorrect-row{ background:#fff4f0; }

/* mobile */
@media (max-width:520px){
  .info-box{ padding:6px; overflow-x:visible; }
  .info-box-inner{ min-width:0; }
  .info-item-wrapper.title{ display:none; }
  .mobile-grid-head{
    display:grid; grid-template-columns:repeat(6,1fr); gap:6px; margin:4px 8px 10px; font-size:12px; color:#555;
  }
  .info-item-wrapper.guess{
    display:grid;
    grid-template-areas:
      "name name name name name name"
      "team pos conf ht age num";
    grid-template-columns:repeat(6,1fr);
    row-gap:6px; column-gap:6px; padding:8px 10px; height:auto; min-height:0; border-radius:12px;
  }
  .info-item-wrapper.guess .name-cell{ grid-area:name; }
  .info-item-wrapper.guess .info-item:nth-child(2){ grid-area:team; }
  .info-item-wrapper.guess .info-item:nth-child(3){ grid-area:pos; }
  .info-item-wrapper.guess .info-item:nth-child(4){ grid-area:conf; }
  .info-item-wrapper.guess .info-item:nth-child(5){ grid-area:ht; }
  .info-item-wrapper.guess .info-item:nth-child(6){ grid-area:age; }
  .info-item-wrapper.guess .info-item:nth-child(7){ grid-area:num; }
  .info-item{ border-right:none !important; font-size:.95em; padding:8px 0; }
  .name-cell{ justify-content:flex-start; font-weight:700; background:#fff !important; }
  .team-cell img{ height:18px !important; margin-bottom:2px !important; }
}

/* --- tighter vertical rhythm (desktop + shared) --- */
.brand{ margin:4px 0 4px; }
.guesses-meter{ margin:2px 0 6px; }
.gm-label{ top:-16px; font-size:13px; }
.clock-wrap{ margin:6px 0 8px; }
input[type="text"]{ margin-bottom:12px; }
button{ margin-top:8px; }

.game-info h3{ margin-bottom:8px; }
.info-box{ padding-top:8px; }

/* --- extra-tight for phones --- */
@media (max-width:520px){
  .brand{ margin:2px 0 2px; }
  .guesses-meter{ gap:5px; margin:2px 0 4px; }
  .gm-label{ top:-14px; font-size:12px; }
  .clock-wrap{ margin:4px 0 6px; }
  input[type="text"]{
    max-width:92vw;
    margin-bottom:10px;
  }
  button{ margin-top:6px; }
}

/* Prevent mobile overflow of the white guess card inside the black outline */
.guess{
  box-sizing: border-box;     /* include the 2px border in the width calc */
  max-width: 100%;
}

/* Give the inner content a hair more breathing room on phones */
@media (max-width:520px){
  .info-box{
    padding-left: 12px;
    padding-right: 12px;
  }
}

/* === fluid mobile sizing (no horizontal scroll) === */
html, body, #root { width:100%; max-width:100%; overflow-x:hidden; }

/* fluid brand */
.brand-head{
  width: clamp(40px, 10vw, 56px);
  height: clamp(40px, 10vw, 56px);
}
.brand-text, .brand-dash, .brand-le{
  font-size: clamp(32px, 9vw, 48px);
}

/* fluid pills + clock */
.gm-seg{ width: clamp(22px, 7vw, 32px); height: clamp(8px, 2.2vw, 10px); }
.shot-clock .digits{ font-size: clamp(22px, 6.4vw, 28px); }

/* inputs/buttons fill but don’t feel huge */
@media (max-width: 520px){
  .App{ padding-left:10px; padding-right:10px; }
  form, .search-container{ width:100%; }
  input[type="text"]{
    width:100%; max-width:none; margin-bottom:10px;
    font-size: clamp(16px, 4.3vw, 18px);
    padding: 10px 12px;
  }
  button{
    font-size: clamp(16px, 4.3vw, 18px);
    padding: 10px 18px;
    margin-top: 8px;
  }

  .game-info{ width:100%; }
  .info-box{
    width: min(96vw, 720px);
    max-width:none;
    margin-left:auto; margin-right:auto;
    padding-left:8px; padding-right:8px;
    box-sizing:border-box;
  }
  .info-box-inner{ min-width:0 !important; }

  .game-info h3{ font-size: clamp(18px, 4.6vw, 20px); }
}

/* tiny phones (<=360px) – nudge spacing down a bit more */
@media (max-width: 360px){
  .guesses-meter{ gap:5px; }
  .brand{ gap:8px; }
}

/* --- FIX: no horizontal scroll + symmetric mobile gutters --- */
html, body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
*, *::before, *::after { box-sizing: border-box; }

/* unified page gutters with iOS safe-area support */
:root { --page-gutter: clamp(12px, 4vw, 20px); }
.App { padding-left: var(--page-gutter); padding-right: var(--page-gutter); }
@supports (padding: max(0px)) {
  .App {
    padding-left: max(var(--page-gutter), env(safe-area-inset-left));
    padding-right: max(var(--page-gutter), env(safe-area-inset-right));
  }
}
@media (max-width: 520px) {
  .search-container, .info-box { width: 100%; margin-left: 0; margin-right: 0; }
  input[type="text"] { width: 100%; max-width: none; }
  :root { --page-gutter: clamp(10px, 4vw, 16px); }
}

/* ===== Streaks / Leaderboard UI ===== */
.streak-fab{
  position: fixed;
  top: 60px;                    /* ← under the help ? */
  right: 16px;
  width: 36px; height: 36px; border-radius: 50%;
  background: #FFD700;
  border: none; cursor: pointer;
  box-shadow: 0 6px 16px rgba(0,0,0,.2);
  font-size: 26px; line-height: 1;
  display:flex; align-items:center; justify-content:center;
  z-index: 3000;
}
.streak-fab:hover{ background:#FFCC00; }

.streak-modal-overlay{
  position: fixed; inset: 0;
  background: rgba(0,0,0,.35);
  z-index: 3100;
  display:flex; align-items:center; justify-content:center;
}
.streak-modal{
  background:#fff;
  border-radius: 12px;
  padding: 22px 18px 18px;
  width: min(680px, 92vw);
  box-shadow: 0 8px 32px rgba(0,0,0,.25);
  position: relative;
  animation: fadeIn .2s;
}
.streak-close{
  position:absolute; top:8px; right:10px;
  background:none; border:none; font-size:22px; cursor:pointer; color:#888;
}
.streak-close:hover{ color:#222; }

.streak-title{
  margin: 0 0 10px;
  font-family: 'Schoolbell', cursive;
  font-size: 26px;
}
.streak-my{ margin-bottom: 10px; }
.streak-row{ display:flex; gap:10px; margin-bottom:10px; }
.streak-chip{
  display:inline-flex; gap:6px; align-items:center;
  padding: 6px 10px; border-radius: 10px;
  border: 2px dashed #FF5910;
  background: #fff9f4; color:#111;
  font-family:'Schoolbell', cursive;
}

.handle-row{
  display:flex; gap:8px; align-items:center; flex-wrap:wrap;
}
.handle-label{ font-weight:600; }
.handle-input{
  flex: 1 1 200px;
  min-width: 160px;
  padding: 8px 10px;
  border:2px solid #000; border-radius:6px;
  font-size: 1rem;
}
.handle-save{
  background:#FFD700; border:none; border-radius:6px; padding:8px 12px; cursor:pointer;
  font-family:'Chakra Petch', serif;
}
.handle-save:hover{ background:#FFCC00; }
.streak-note{ margin-top:6px; font-size:.95rem; color:#555; }

.lb-title{
  margin: 10px 0 6px;
  font-family: 'Chakra Petch', serif;
  font-size: 16px;
}
.lb-box{ max-height: 50vh; overflow:auto; border:1px solid #eee; border-radius:10px; }

.streak-table{
  width:100%; border-collapse: collapse; background:#fff;
  font-family: 'Roboto', sans-serif;
}
.streak-table th, .streak-table td{
  padding: 10px 12px; border-bottom:1px solid #f0f0f0; text-align:left;
}
.streak-table thead th{
  position: sticky; top:0; background:#fff; z-index:1;
}
.streak-table tr.me td{
  background: #fff7d6;
  font-weight: 600;
}
.lb-loading, .lb-error{
  padding: 16px; text-align:center;
}

/* ===== Team picker ===== */
.team-picker{ margin-top:10px; }
.team-picker-label{ font-weight:600; margin-bottom:6px; text-align:left; }
.team-grid{
  display:grid; grid-template-columns: repeat(6, 44px); gap:8px;
  justify-content:flex-start;
}
.team-btn{
  width:44px; height:44px; border-radius:10px; border:2px solid #ddd; background:#fff;
  display:flex; align-items:center; justify-content:center; cursor:pointer;
}
.team-btn img{ width:28px; height:28px; object-fit:contain; }
.team-btn.active{ border-color:#FF5910; box-shadow:0 0 0 3px #ff591033; }
@media (max-width:520px){
  .team-grid{ grid-template-columns: repeat(6, 40px); gap:6px; }
  .team-btn{ width:40px; height:40px; }
  .team-btn img{ width:24px; height:24px; }
}
.handle-cell{ display:flex; align-items:center; gap:8px; }
.lb-team{ width:18px; height:18px; object-fit:contain; }

/* ===== Archive Day FAB, modal & banner ===== */
.archive-fab{
  position: fixed;
  top: 108px;                 /* ← stacked below streak-fab */
  right: 16px;
  width: 36px; height: 36px; border-radius: 50%;
  background: #fff; color:#111; border:2px solid #000; cursor: pointer;
  box-shadow: 0 6px 16px rgba(0,0,0,.2);
  font-size: 24px; line-height: 1;
  display:flex; align-items:center; justify-content:center;
  z-index: 3000;
}
.archive-fab:hover{ background:#f7f7f7; }

.archive-modal-overlay{
  position: fixed; inset: 0;
  background: rgba(0,0,0,.35);
  z-index: 3200;
  display:flex; align-items:center; justify-content:center;
}
.archive-modal{
  background:#fff; border-radius:12px; padding:22px 18px 18px;
  width:min(420px, 92vw);
  box-shadow: 0 8px 32px rgba(0,0,0,.25);
  position:relative; animation: fadeIn .2s;
}
.archive-close{
  position:absolute; top:8px; right:10px; background:none; border:none; font-size:22px; color:#888; cursor:pointer;
}
.archive-close:hover{ color:#222; }
.archive-title{ margin:0 0 6px; font-family:'Schoolbell', cursive; font-size:24px; }
.archive-sub{ margin:0 0 12px; font-size:.95rem; color:#555; }
.archive-row{ display:flex; gap:10px; align-items:center; justify-content:center; margin-bottom:12px; }
.archive-row input[type="date"]{
  border:2px solid #000; border-radius:6px; padding:8px 10px; font-size:1rem;
}
.archive-actions{ display:flex; gap:10px; justify-content:center; }
.archive-go{ background:#FFD700; padding:8px 12px; border-radius:8px; border:none; }
.archive-go:hover{ background:#FFCC00; }
.archive-cancel{ background:#eee; padding:8px 12px; border-radius:8px; border:none; }

.archive-banner{
  position:sticky; top:0; z-index:1500;
  background:#fff3c4; border-bottom:2px solid #000; padding:8px 10px;
  display:flex; align-items:center; justify-content:center; gap:10px;
}
.archive-exit{
  background:#fff; border:2px solid #000; border-radius:8px; padding:4px 10px; cursor:pointer;
}
.archive-exit:hover{ background:#f7f7f7; }

/* ===== Inline actions (Streaks + Archive) ===== */
.inline-actions{
  display:flex;
  gap:10px;
  justify-content:center;
  align-items:center;
  margin:8px 0 12px;
  flex-wrap:wrap;
}

.ia-btn{
  display:inline-flex;
  align-items:center;
  justify-content:center;
  gap:6px;
  padding:10px 16px;
  border-radius:10px;
  border:none;
  cursor:pointer;
  font-family:'Chakra Petch',serif;
  box-shadow:0 2px 8px rgba(0,0,0,.12);
  background:#FFD700;
  color:#1A1A1A;
}
/* keep inline buttons tight (don’t inherit global button margin) */
.inline-actions .ia-btn{ margin-top:0; }

.ia-btn:hover{ background:#FFCC00; }

.ia-secondary{
  background:#fff;
  border:2px solid #000;
  box-shadow:0 2px 8px rgba(0,0,0,.08);
}
.ia-secondary:hover{ background:#f7f7f7; }

/* stack on phones */
@media (max-width:520px){
  .inline-actions{
    flex-direction:column;
    align-items:center;
    gap:8px;
  }
  .ia-btn{
    width:min(360px, 92vw);
  }
}
